<style lang="scss">


.specName{
  width: 100%;
  line-height: 40px;
  height: 40px;
  margin:0;
  padding: 0;
}
.specName2{
  border-bottom:1px solid #ccc;
}
.app-table .el-table__row .rowspanClass .cell{
  padding: 0;
}
.app-table2 .el-table .cell{
  padding: 0;
  text-align: center;
}
</style>

<template lang="html">

<div class="">
    <h3 class="h3-t"></h3>
    <!-- btn -->
    <div class="btn-box">
        <el-button type="success" @click='addHandler'></el-button>
        <el-button type="warning" @click='updateHandler'></el-button>
        <el-button type="danger" @click='deleteHandler'></el-button>
    </div>
    <!-- table list -->
    <div class="app-table app-table2">
        <el-table tooltip-effect="dark" @selection-change="handleSelectionChange" :data="tableData" border style="width: 100%">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="productName" label=""></el-table-column>
            <el-table-column prop="title" label=""></el-table-column>
            <el-table-column prop="subTitle" label=""></el-table-column>
            <el-table-column prop="describe" label=""></el-table-column>
            <el-table-column label="">
              <template scope="scope">
                <span v-for="(item,index) in scope.row.images.split(',')"><img width="60" height="60" :src="item" alt=""></span>
              </template>
                <!-- <span>{{scope.row.specs[0].specStatus}}</span> -->

            </el-table-column>
            <el-table-column label="" >
              <template scope="scope">
                <div :class="{'specName':true,'specName2':scope.row.specs.length!=(index+1)}"  v-for="(item,index) in scope.row.specs">{{item.specName}}</div>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template scope="scope">
                <div :class="{'specName':true,'specName2':scope.row.specs.length!=(index+1)}"   v-for="(item,index) in scope.row.specs">{{item.specPrice}}</div>
              </template>
            </el-table-column>
            <el-table-column prop="" label="">
              <template scope="scope">
                <div :class="{'specName':true,'specName2':scope.row.specs.length!=(index+1)}"  v-for="(item,index) in scope.row.specs">{{item.address}}</div>
              </template>

            </el-table-column>
            <el-table-column label="">
              <template scope="scope">
                <div :class="{'specName':true,'specName2':scope.row.specs.length!=(index+1)}"  v-for="(item,index) in scope.row.specs">
                  <span v-if="item.specStatus=='0'"></span>
                  <span v-else></span>
                </div>
              </template>
            </el-table-column>
        </el-table>
    </div>
    <!--  -->
    <add-update :dialogType='dialogType' ref="refDialog" @listenToSuccess='listenToSuccess'></add-update>
    <del-dialog delTitle='' ref="refDelDialog" @listenToDel='listenToDel'></del-dialog>


    <!--  -->

</div>

</template>

<script>
import qs from 'qs'
import AddUpdate from './AddUpdate'
import DelDialog from '../../components/DelDialog'
export default {
    components : {
        AddUpdate,
        DelDialog
    },
    data() {
            return {
                tableData: [],
                dialogType:'',
                multipleSelection:[],
            }
        },
        create() {

        },
        mounted() {
            this.list()

        },
        updated() {

        },
        methods: {
          // table 
            handleSelectionChange(val) {
                console.log(val)
                this.multipleSelection = val
            },
            // 
            list() {
                        this.$http.get(this.HttpUrl.UrlConfig.goodList)
                            .then(res => {
                                console.log(res)
                                res = res.data
                                this.tableData = res
                            }).catch(error => {
                                console.log(error)
                            })
                },

                // 
                addHandler() {
                    this.$refs.refDialog.childMethod('add','')
                },
                // 
                updateHandler() {
                  if (this.multipleSelection.length != 1) {
                      this.$message({
                          message: '',
                          type: 'warning'
                      });
                      return;
                  }
                  this.$refs.refDialog.childMethod('update',this.multipleSelection[0].productId)
                },
                // 
                deleteHandler() {
                  if (this.multipleSelection.length != 1) {
                      this.$message({
                          message: '',
                          type: 'warning'
                      });
                      return;
                  }
                  this.$refs.refDelDialog.childMethod()
                },
                listenToSuccess(data){
                  if(data.ret=='0'){
                    this.list()
                  }else{
                    this.$message({
                        message: data.msg,
                        type: 'warning'
                    });
                  }
                },
                listenToDel(data){
                  let datas = {
                    'goodId':this.multipleSelection[0].productId
                  }
                  this.$http.post(this.HttpUrl.UrlConfig.goodDel,qs.stringify(datas))
                      .then(res => {
                          res = res.data
                          if (res.ret == 0) {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.list()
                            this.$refs.refDelDialog.delSucess()
                          } else {
                              this.$message.error(res.msg);
                          }
                      }).catch(error => {
                          console.log(error)
                      })
                }

        }
}

</script>



