<style lang="css">

.input2 {
    width: 100px;
}

.specList {
    padding: 0 0 10px;
}

.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
}

.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 80px;
    height: 80px;
    line-height: 80px;
    text-align: center;
}

.avatar {
    width: 80px;
    height: 80px;
    display: block;
}

</style>

<template lang="html">

<div class="">
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" size="small" @close="resetForm('ruleForm')">
        <div class="dialog-form">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px">
                <el-form-item label="" prop="name">
                    <el-input class="input" v-model="ruleForm.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="mainTxt">
                    <el-input class="input" v-model="ruleForm.mainTxt" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="viceTxt">
                    <el-input class="input" v-model="ruleForm.viceTxt" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="intro">
                    <el-input class="input" type="textarea" v-model="ruleForm.intro" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="imageUrl">
                    <el-upload class="upload-demo" :action="uploadify" :on-success="handleAvatarSuccess" :on-preview="handlePreview" :on-remove="handleRemove" :file-list="fileList2" list-type="picture">
                        <el-button size="small" type="primary"></el-button>
                        <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                    </el-upload>
                    <el-input type="hidden" style="display:none;" v-model="ruleForm.imageUrl"></el-input>
                </el-form-item>
                <el-form-item label="" prop="specDatas">
                    <div v-if="ruleForm.specDatas.length!=0" class="specList" v-for="(specItem,specIndex) in ruleForm.specDatas" :key="specIndex">
                        {{specIndex+1}}
                        <el-input class="input2" type="text" v-model="specItem.specName" placeholder=""></el-input>&nbsp;&nbsp;&nbsp;&nbsp;
                        <el-input class="input2" type="text" v-model="specItem.specPrice" placeholder=""></el-input>&nbsp;&nbsp;&nbsp;&nbsp;
                        <el-button type='text' @click="handleDelSpecBtn(specIndex)"></el-button>
                    </div>
                    <el-button type='text' @click="handleSpecBtn"></el-button>
                </el-form-item>

                <el-form-item label="" prop="specifications">
                    <el-input class="input" v-model="ruleForm.specifications" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="address">
                    <el-input class="input" v-model="ruleForm.address" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="content">
                    <vue-editor v-model="ruleForm.content"></vue-editor>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                    <el-button></el-button>
                </el-form-item>
            </el-form>
        </div>
    </el-dialog>
</div>

</template>

<script>
import qs from 'qs'
import {
    VueEditor
}
from 'vue2-editor'
export default {
    props: ['dialogType'],
    components: {
        VueEditor
    },
    data() {
        return {
            fileList2: [],
            uploadify: '',
            goodId: '',
            isUpdate: false,
            dialogVisible: false,
            dialogTitle: '',
            ruleForm: {
                name: '',
                mainTxt: '',
                viceTxt: '',
                intro: '',
                specifications: '',
                address: '',
                specDatas: [],
                imageUrl: '',
                content: ''
            },
            rules: {
                name: [{
                    required: true,
                    message: '20',
                    trigger: 'blur change',
                    max: 20,
                    min: 1
                }],
                mainTxt: [{
                    required: true,
                    message: '20',
                    trigger: 'blur change',
                    max: 20,
                    min: 1
                }],
                viceTxt: [{
                    required: true,
                    message: '20',
                    trigger: 'blur change',
                    max: 20,
                    min: 1
                }],
                intro: [{
                    required: true,
                    message: '100',
                    trigger: 'blur change',
                    max: 100,
                    min: 1
                }],
                specifications: [{
                    required: true,
                    message: '30',
                    trigger: 'blur change',
                    max: 30,
                    min: 1
                }],
                address: [{
                    required: true,
                    message: '20',
                    trigger: 'blur change',
                    max: 20,
                    min: 1
                }],
                content: [{
                    required: true,
                    message: '',
                    trigger: 'blur change',
                }],
                imageUrl: [{
                    required: true,
                    message: '',
                    trigger: 'blur change'
                }],
                specDatas: [{
                    type: 'array',
                    required: true,
                    message: '',
                    trigger: 'blur change'
                }],
            },

        }
    },
    created() {
        this.uploadify = this.HttpUrl.UrlConfig.upload
    },
    mounted() {









    },
    methods: {
        childMethod(type, goodId) {
                this.goodId = goodId
                this.dialogVisible = true
                if (type == 'add') {
                    this.isUpdate = false
                    this.dialogTitle = ''
                } else if (type == 'update') {
                    this.detail()
                    this.isUpdate = true
                    this.dialogTitle = ''
                }
            },
            // 
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {

                        this.handleSubmitServer();
                    } else {
                        return false;
                    }
                });
            },
            // 
            handleSubmitServer() {
                var imgUrl = []
                this.fileList2.forEach((val) => {
                    imgUrl.push(val.response)
                })
                console.log(imgUrl.join(','))
                let datas = {
                    'productName': this.ruleForm.name,
                    'title': this.ruleForm.mainTxt,
                    'subTitle': this.ruleForm.viceTxt,
                    'describe': this.ruleForm.intro,
                    'images': this.ruleForm.imageUrl,
                    'specs': JSON.stringify(this.ruleForm.specDatas),
                    'specsTips': this.ruleForm.specifications,
                    'address': this.ruleForm.address,
                    'content': this.ruleForm.content
                }
                console.log(datas)
                let url = '',
                    message = ''
                if (this.isUpdate == true) {
                    url = this.HttpUrl.UrlConfig.goodUpdate
                    message = ''
                } else {
                    url = this.HttpUrl.UrlConfig.goodAdd
                    message = ''
                }
                this.$http.post(url, datas)
                    .then(res => {
                        res = res.data
                        if (res.ret === 0) {
                            this.$emit("listenToSuccess", res)
                            this.$message({
                                message: message,
                                type: 'success'
                            });
                            this.dialogVisible = false
                        } else {
                            this.$message.error(res.msg);
                        }
                    }).catch(error => {
                        console.log(error)
                    })
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            handleSpecBtn() {
                this.ruleForm.specDatas.push({
                    'specName': '',
                    'specPrice': ''
                })
            },
            handleDelSpecBtn(specIndex) {
                this.ruleForm.specDatas.splice(specIndex, 1)
            },
            // 
            detail() {
                let datas = {
                    'id': this.goodId
                  }
                this.$http.post(this.HttpUrl.UrlConfig.detail, qs.stringify(datas))
                    .then(res => {
                        res = res.data
                        this.ruleForm.name = res.productName
                        this.ruleForm.mainTxt = res.title
                        this.ruleForm.viceTxt = res.subTitle
                        this.ruleForm.intro = res.describe
                        this.ruleForm.address = res.specs[0].address
                        this.ruleForm.content = res.content
                        this.ruleForm.specifications = res.specTips

                      res.images.split(',').forEach((val)=>{
                          this.fileList2.push({
                            "status": "success",
                            "name": val,
                            "size": '',
                            "percentage": 100,
                            "uid": '',
                            "raw": {
                              "uid": ''
                            },
                            "url": val,
                            "response": val
                          }
                          )
                      })

                      res.specs.forEach((val)=>{
                        this.ruleForm.specDatas.push({
                            'specName': val.specName,
                            'specPrice': val.specPrice
                        })
                      })



                    }).catch(error => {
                        console.log(error)
                    })
            },
            handleAvatarSuccess(res, file, fileList) {

              this.fileList2 = fileList
                // this.ruleForm.imageUrl = res;

                // this.fileList2 = fileList

                let imgStr = []
                fileList.forEach((val)=>{
                  imgStr.push(val.response)
                })

                this.ruleForm.imageUrl = imgStr.join(',')

                console.log(this.ruleForm.imageUrl)
                if(imgStr.length!=0){
                  this.rules.imageUrl[0].required = false
                }else{
                  this.rules.imageUrl[0].required = true
                }
                // console.log(imgStr.join(','))

            },
            handleRemove(file, fileList) {
                this.fileList2 = fileList
                // this.fileList2 = fileList
                let imgStr = []
                fileList.forEach((val)=>{
                  imgStr.push(val.response)
                })
                this.ruleForm.imageUrl = imgStr.join(',')
                if(imgStr.length!=0){
                  this.rules.imageUrl[0].required = false
                }else{
                  this.rules.imageUrl[0].required = true
                }

                console.log(this.ruleForm.imageUrl)
            },
            handlePreview(file) {
                console.log(file);
            }
    }
}

</script>



