<style lang="scss">


.batchLook{
  color:#3c8dbc
}
</style>

<template lang="html">

<div class="">
    <h3 class="h3-t"></h3>
    <!-- btn -->
    <div class="btn-box">
        <el-button type="success" @click='addHandler'></el-button>
    </div>
    <!-- table list -->
    <div class="app-table">
        <el-table ref="multipleTable" tooltip-effect="dark" :data="tableData" border style="width: 100%">
            <el-table-column width="80" type="index" label=""></el-table-column>
            <el-table-column prop="createTime" label=""></el-table-column>
            <el-table-column prop="goodsName" label=""></el-table-column>
            <el-table-column prop="goodSpecName" label=""></el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="number" label=""></el-table-column>
            <el-table-column prop="usedNum" label=""></el-table-column>
            <el-table-column prop="goodsCode" label="">
              <template scope="scope">
                <span class="batchLook" @click="clickBatchLook(scope.row.batchCode)"></span>
              </template>
            </el-table-column>
        </el-table>
        <div class="pager-box" v-if="total>pageSize">
                <el-pagination @current-change="handleCurrentChange" :page-size="pageSize" layout="total, prev, pager, next" :total="total">
                </el-pagination>
            </div>
    </div>

    <el-dialog title="" :visible.sync="dialogVisible" size="small" @close="resetForm('ruleForm')">
        <div class="dialog-form">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px">
                <el-form-item label="" prop="goodsInfo">
                  <el-select v-model="ruleForm.goodsInfo" placeholder="" @change="goodsInfoChange">
                    <el-option v-for="(item,index) in getGoodsInfoArr" :key="item.id" :label="item.name" :value="item.id"> {{item.name}}</el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" prop="goodSpec">
                  <el-select v-model="ruleForm.goodSpec" placeholder="" @change="goodSpecChange">
                    <el-option v-for="(item,index) in getGoodSpecArr" :key="item.id" :label="item.specName" :value="item.id"> {{item.specName}}</el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="" prop="name">
                  <el-input class="input"  v-model="ruleForm.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="number">
                  <el-input class="input"  v-model="ruleForm.number" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </el-dialog>



    <el-dialog title="" :visible.sync="dialogBatchVisible" size="large">
        <div class="dialog-form">
          <el-table ref="multipleTable" tooltip-effect="dark" :data="tableData2" border style="width: 100%">
              <el-table-column width="80" type="index" label=""></el-table-column>
              <el-table-column prop="goodsCode" label=""></el-table-column>
              <el-table-column prop="createTime" label=""></el-table-column>
              <el-table-column prop="name" label=""></el-table-column>
              <el-table-column prop="goodsName" label=""></el-table-column>
              <el-table-column prop="goodSpecName" label=""></el-table-column>
          </el-table>
          <div class="pager-box" v-if="total2>pageSize2">
              <el-pagination @current-change="handleCurrentChange2" :page-size="pageSize2" layout="total, prev, pager, next" :total="total2">
              </el-pagination>
          </div>
        </div>
    </el-dialog>
</div>

</template>

<script>
import qs from 'qs'
export default {
    data() {
            return {
                tableData: [],
                tableData2: [],
                dialogVisible:false,
                dialogBatchVisible:false,
                pageNo: 0,
                pageSize: 10,
                total: null,
                pageNo2: 0,
                pageSize2: 4,
                total2: null,
                getGoodsInfoArr:[],
                getGoodSpecArr:[],
                ruleForm: {
                    goodsInfo:'',
                    goodSpec:'',
                    name:'',
                    number:''
                },
                rules: {
                    // goodsInfo: [{
                    //     required: true,
                    //     message: '',
                    //     trigger: 'change',
                    // }],
                    // goodSpec: [{
                    //     required: true,
                    //     message: '',
                    //     trigger: 'change',
                    // }],
                    // name: [{
                    //     required: true,
                    //     message: '',
                    //     trigger: 'blur change'
                    // }],
                    // number: [{
                    //     required: true,
                    //     message: '',
                    //     trigger: 'blur change'
                    // }]
                },
                specId:'',
                goodId:'',
                batchCode:''
            }
        },
        create() {

        },
        mounted() {
          this.batchList()
          this.getGoodsInfo()
        },
        updated() {

        },
        methods: {
                batchList(){
                  this.$http.get(this.HttpUrl.UrlConfig.batchList+'?pageNo='+this.pageNo + '&pageSize='+this.pageSize)
                      .then(res => {
                          res = res.data
                          this.tableData = res.batchList
                          this.total =parseInt(res.listCount)

                      }).catch(error => {
                          console.log(error)
                      })
                },
                handleCurrentChange(val) {
                    this.pageNo = parseInt(val)-1;
                    this.batchList()
                },
                handleCurrentChange2(val) {
                    this.pageNo2 = parseInt(val)-1;
                    this.viewGoodsCode(this.batchCode)
                },
                // 
                addHandler() {
                    this.dialogTitle = ""
                    this.dialogVisible = true

                },
                // 
                getGoodsInfo(){
                  this.$http.get(this.HttpUrl.UrlConfig.getGoodsInfo)
                      .then(res => {
                          res = res.data
                          this.getGoodsInfoArr = res

                      }).catch(error => {
                          console.log(error)
                      })
                },
                goodsInfoChange(val){
                  console.log(val)
                  if(val){
                    this.goodId = val
                    this.getGoodSpecArr = []
                    this.ruleForm.goodSpec = ''
                    this.getGoodSpec(val)
                  }

                },
                goodSpecChange(val){
                  console.log(val)
                  if(val){
                    this.specId = val
                  }


                },
                // 
                getGoodSpec(goodId){
                  this.$http.get(this.HttpUrl.UrlConfig.getGoodSpec + "?goodId="+goodId)
                      .then(res => {
                          res = res.data
                          this.getGoodSpecArr = res
                      }).catch(error => {
                          console.log(error)
                      })
                },
                submitForm(formName) {
                    this.$refs[formName].validate((valid) => {
                        if (valid) {
                            this.handleSubmitServer();
                        } else {
                            return false;
                        }
                    });
                },
                resetForm(formName) {
                    this.$refs[formName].resetFields();
                },
                // 
                handleSubmitServer() {
                    let datas = {
                      'goodId':this.goodId,
                      'specId':this.specId,
                      'name':this.ruleForm.name,
                      'number':this.ruleForm.number
                    }
                    // console.log(datas)
                    this.$http.post(this.HttpUrl.UrlConfig.batchAdd,qs.stringify(datas))
                        .then(res => {
                            res = res.data
                            if(res.ret == '0'){
                              this.$message({
                                  message: '',
                                  type: 'success'
                              });
                              this.dialogVisible = false
                              this.batchList()
                            }else{
                              this.$message({
                                  message: res.msg,
                                  type: 'warning'
                              });
                            }

                        }).catch(error => {
                            console.log(error)
                        })
                },
                clickBatchLook(batchCode){
                  this.batchCode = batchCode
                  this.dialogBatchVisible = true
                  this.tableData2 = []
                  this.viewGoodsCode(batchCode)
                },
                viewGoodsCode(batchCode){
                  this.$http.get(this.HttpUrl.UrlConfig.viewGoodsCode+'?batchCode='+batchCode + '&pageNo=' + this.pageNo2 + '&pageSize='+this.pageSize2)
                      .then(res => {
                          res = res.data
                          this.tableData2 = res.batchInfos
                          this.total2 = parseInt(res.listCount)
                      }).catch(error => {
                          console.log(error)
                      })
                }
        }
}

</script>



